import client, { rootUrl } from '../api/configAxios';
import { logger } from '../utils';

const getRequest = async (url: string) => {
  try {
    const response = await client.get(`${rootUrl}${url}`);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error getRequest', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

const postRequest = async (url: string, data: any, options?: any) => {
  try {
    const response = await client.post(`${rootUrl}${url}`, data, options);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error postRequest', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

const putRequest = async (url: string, data: any) => {
  try {
    const response = await client.put(`${rootUrl}${url}`, data);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error putRequest', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.response?.status || error?.status,
    };
  }
};

const deleteRequest = async (url: string) => {
  try {
    const response = await client.delete(`${rootUrl}${url}`);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error deleteRequest', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

export { getRequest, postRequest, putRequest, deleteRequest };
